import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import { useMutation, useQuery, useQueryClient } from '@tanstack/react-query';
import { useFormik } from 'formik';
import * as yup from 'yup';

import TextField from '@mui/material/TextField';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import FormHelperText from '@mui/material/FormHelperText';
import Select from '@mui/material/Select';

import Loader from '../components/Loader';

import { UserCreate, UserQuery } from '../api';
import { useSnackbar } from '../hooks';
import { User, CreateUser, UserRoles } from '../utils/dataTypes';

export default function ViewUserForm() {
  const { pathname } = useLocation();
  const navigate = useNavigate();
  const queryClient = useQueryClient();
  const setSnackbarMessage = useSnackbar();

  const {
    isLoading: loading,
    isFetching: fetching,
    data,
  } = useQuery({
    queryKey: ['user', pathname.split('/')[3]],
    queryFn: UserQuery,
    keepPreviousData: true,
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const { isLoading } = useMutation(UserCreate, {
    onSuccess: () => {
      setSnackbarMessage({
        severity: 'success',
        message: 'Created',
      });
      queryClient.invalidateQueries(['users']);
      navigate(-1);
    },
    onError: (error: Error) => {
      setSnackbarMessage({
        severity: 'error',
        message: error instanceof Error ? error.message : JSON.stringify(error),
      });
    },
  });

  const formik = useFormik<CreateUser>({
    initialValues: data || {
      fullName: '',
      phone: '',
      userRole: 'Admin',
      jobTitle: '',
      email: '',
      password: '',
    },
    enableReinitialize: true,
    onSubmit: () => {
      return;
    },
  });

  const handleClose = () => {
    navigate(-1);
  };

  return (
    <Box>
      <form onSubmit={formik.handleSubmit} onReset={formik.handleReset}>
        <Box key="controls" sx={{ p: 1, display: 'inline-flex' }}>
          <Box key="left">
            <TextField
              sx={{ p: 1 }}
              fullWidth
              size="small"
              id="fullName"
              key="fullName"
              name="fullName"
              label="Full Name"
              value={formik.values.fullName}
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              error={formik.touched.fullName && Boolean(formik.errors.fullName)}
              helperText={formik.touched.fullName && formik.errors.fullName}
            />
            <FormControl
              sx={{ p: 1 }}
              fullWidth
              size="small"
              key="userRole"
              error={formik.touched.userRole && Boolean(formik.errors.userRole)}
            >
              <InputLabel id="userRole">User Role</InputLabel>
              <Select
                labelId="demo-simple-select-label"
                id="userRole"
                name="userRole"
                label="User Role"
                value={formik.values.userRole}
                onChange={formik.handleChange}
                onBlur={formik.handleBlur}
              >
                {UserRoles.map((role) => (
                  <MenuItem key={role} value={role}>
                    {role}
                  </MenuItem>
                ))}
              </Select>
              <FormHelperText>
                {formik.touched.userRole && formik.errors.userRole}
              </FormHelperText>
            </FormControl>
            <TextField
              sx={{ p: 1 }}
              fullWidth
              size="small"
              id="jobTitle"
              key="jobTitle"
              name="jobTitle"
              label="Job Title"
              value={formik.values.jobTitle}
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              error={formik.touched.jobTitle && Boolean(formik.errors.jobTitle)}
              helperText={formik.touched.jobTitle && formik.errors.jobTitle}
            />
          </Box>
          <Box key="right">
            <TextField
              sx={{ p: 1 }}
              fullWidth
              size="small"
              id="phone"
              key="phone"
              name="phone"
              label="Phone"
              value={formik.values.phone}
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              error={formik.touched.phone && Boolean(formik.errors.phone)}
              helperText={formik.touched.phone && formik.errors.phone}
            />
            <TextField
              sx={{ p: 1 }}
              fullWidth
              size="small"
              id="email"
              key="email"
              name="email"
              label="Email"
              value={formik.values.email}
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              error={formik.touched.email && Boolean(formik.errors.email)}
              helperText={formik.touched.email && formik.errors.email}
            />
            <TextField
              sx={{ p: 1 }}
              fullWidth
              size="small"
              id="password"
              key="password"
              name="password"
              label="Password"
              type="password"
              value={formik.values.password}
              onChange={formik.handleChange}
              onBlur={formik.handleBlur}
              error={formik.touched.password && Boolean(formik.errors.password)}
              helperText={formik.touched.password && formik.errors.password}
            />
          </Box>
        </Box>
        <Box key="buttons" sx={{ p: 1, display: 'inline-flex' }}>
          <Button
            fullWidth
            variant="contained"
            size="small"
            color="primary"
            type="button"
            onClick={handleClose}
          >
            Cancel
          </Button>
        </Box>
      </form>
      <Loader visible={loading || fetching || isLoading} />
    </Box>
  );
}
